<template>
    <div id="board">
        <h2>Ingresa tu Correo Electrónico</h2>
        <div id="msgSuccess">{{msg_succesful}}</div>
        <input id="f_email" name="email" type="email" @click="msgDisplay" placeholder="Correo Electrónico">

        <button type="button" class="btn-send btn btn-primary mt-4" @click="sendEmail">Enviar</button>
    </div>
</template>

<script>
import axios from 'axios'

export default {
    name: 'MyForgetPasswd',
    data(){
        return {
            msg_succesful: "",
        }
    },
    mounted(){
        this.msgDisplay();
    },
    methods: {
        sendEmail(){
            const form = document.getElementById("f_email");
            var object = {};
            object[form.name] = form.value;

            axios
                .post("/api/ForgotPassword/sendEmail",object)
                .then(response =>{
                    this.msg = response.data
                    console.log(this.msg.StatusCorreo);
                    if(this.msg.StatusCorreo == true){
                        this.msg_succesful ="Se envio exitosamente el enlace a su correo";
                        document.getElementById("msgSuccess").style.backgroundColor="#A3DDCB";
                        document.getElementById("msgSuccess").style.display="block";
                    }else{
                        this.msg_succesful = "Error: " + this.msg.msj
                        document.getElementById("msgSuccess").style.backgroundColor="#DBE2EF";
                        document.getElementById("msgSuccess").style.display="block";
                    }
                })
                .catch(error => {
                    console.log(error)
                })
        },
        msgDisplay(){
            document.getElementById("msgSuccess").style.display="none";
        },
    }
}
</script>

<style scoped>

#msgSuccess{
    background-color: #A3DDCB;
    width: 350px;
    padding: 0.6rem 1.2rem 1rem 1.2rem;
    margin: 20px auto;
    height: 45px;
    display: none;
}

#board{
    height: 600px;
    text-align: center;
    padding-top: 5rem;
    padding-bottom: 9rem;
    background-color: #F2F5F9;
}

h2{
    padding-top: 5rem;
    margin-bottom: 2rem;
    color: #2c3e50;
}

div> input{
    padding: 0.5rem 1rem 0.5rem 1rem;
    display: block;
    margin: auto;   
}


input,button{
    width: 300px;
    padding: 0.5rem 1rem 0.5rem 1rem;
}

input{
    width: 350px;
    margin-top: 15px;
}


.btn-send{
    background-color: #03506F !important;
    border-color: #03506F !important;
}

.btn-send:hover{
    background-color: #0f87b7 !important;
    border-color: #0f87b7 !important;
}


</style>
